import React from "react";

import { Typography, Rating, Link, Grid } from "@mui/material";

function ListItem(props) {
  return (
    <Grid container direction="row" alignItems="center" columnSpacing={2}>
      <Grid item sx={{ mt: 0.5 }}>
        <Typography variant="p">
          <Rating
            name="size-small"
            value={props.stars}
            max={props.max}
            icon={props.icon}
            size="small"
            readOnly
          />
        </Typography>
      </Grid>
      <Grid item>
        <Link href={props.href} underline="none">
          <Typography variant="p">{props.text}</Typography>
        </Link>
      </Grid>
    </Grid>
  );
}

export default ListItem;
